

























import Vue from "vue";

export default Vue.extend({
  name: "ProgressBar",
  props: {
    value: {
      type: Number,
      default: 0,
    },
  },
  data: function () {
    return {
      progressBarWidth: 0,
    };
  },
  mounted() {
    setTimeout(() => {
      this.setBarWidth(this.value);
    }, 50);
  },
  watch: {
    value(width) {
      this.setBarWidth(width);
    },
  },
  methods: {
    setBarWidth(width: number) {
      this.progressBarWidth =
        width && /^\d\d*(\.\d+)?$/.test(width.toString())
          ? width
          : this.progressBarWidth;
    },
  },
});
